import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class IndexService {
  public subject = new BehaviorSubject<any[]>([]);

  constructor(private http: HttpClient) {}

  get subdata() {
    return this.subject.asObservable();
  }

  postCourse(data: any) {
    return this.http.post(environment.apibaseurl + '/postCourse', data);
  }

  getCourse() {
    return this.http.get(environment.apibaseurl + '/getCourse');
  }

  getSingleCourse(id: any) {
    console.log(id);
    return this.http.get(environment.apibaseurl + `/getsingleCourse/${id}`);
  }

  GetConceptsOfsubj(data: any) {
    return this.http.post(
      environment.apibaseurl + `/getconceptsofsubject`,
      data
    );
  }

  postConcepts(data: any) {
    return this.http.post(environment.apibaseurl + '/postConcepts', data);
  }

  posttrail(data: any) {
    return this.http.post(environment.apibaseurl + '/posttrail', data);
  }

  getQuestionsData(id: string) {
    return this.http.get(environment.apibaseurl + `/getQuestionsData/${id}`);
  }

  deletedoc(id) {
    return this.http.post(environment.apibaseurl + '/deletedoc', { id });
  }
  deleteoneQue(id: string) {
    console.log(id);
    return this.http.post(environment.apibaseurl + '/deleteoneque', { id });
  }

  editSingleQnA(data: any) {
    return this.http.post(environment.apibaseurl + '/editSingleQue', data);
  }
}
