import { Component, InputDecorator, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { IndexService } from 'src/app/Shared/index.service';
import io from 'socket.io-client';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-add-subject',
  templateUrl: './add-subject.component.html',
  styleUrls: ['./add-subject.component.scss'],
})
export class AddSubjectComponent implements OnInit {
  courseName: string;
  description: string;
  subject: any;
  socket: any;

  constructor(
    public modalctrl: ModalController,
    private indexservice: IndexService
  ) {
    this.socket = io(environment.apibaseurl);
  }

  ngOnInit() {}

  submit() {
    this.subject = this.subject.split(',');
    console.log(this.subject);

    let data = {
      courseName: this.courseName,
      description: this.description,
      subjects: this.subject,
    };
    this.indexservice.postCourse(data).subscribe(
      (res: any) => {
        this.socket.emit('getCourses', {});
        console.log(res);
        if (!res.err) {
          this.modalctrl.dismiss({
            dismissed: true,
          });
        }
      },
      (err) => {
        console.log(err);
      }
    );
  }

  dismiss() {
    this.modalctrl.dismiss({
      dismissed: true,
    });
  }
}
