import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'auth',
    pathMatch: 'full',
  },
  {
    path: 'folder/:id',
    loadChildren: () =>
      import('./folder/folder.module').then((m) => m.FolderPageModule),
  },
  {
    path: 'flash/:id',
    loadChildren: () =>
      import('./flash/flash.module').then((m) => m.FlashPageModule),
  },
  {
    path: 'concept/:id',
    loadChildren: () =>
      import('./Pages/conceptData/levels.module').then(
        (m) => m.LevelsPageModule
      ),
  },
  {
    path: 'subjects/:id',
    loadChildren: () =>
      import('./Pages/subjects/subjects.module').then(
        (m) => m.SubjectsPageModule
      ),
  },
  {
    path: 'auth',
    loadChildren: () =>
      import('./Authentication/auth/auth.module').then((m) => m.AuthPageModule),
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
